// initial state
const state = {
  user: {
    sexEnum: [{ key: '1', value: '' }, { key: '2', value: '' }],
    statusEnum: [{ key: '1', value: '' }, { key: '0', value: '' }],
    levelEnum: [{ key: 1, value: '' }, { key: 2, value: '' }, { key: 3, value: '' }, { key: 4, value: '' }, { key: 5, value: '' }, { key: 6, value: '' },
      { key: 7, value: '' }, { key: 8, value: '' }, { key: 9, value: '' },
      { key: 10, value: '' }, { key: 11, value: '' }, { key: 12, value: '' }],
    roleEnum: [{ key: 1, value: '' }, { key: 2, value: '' }, { key: 3, value: '' }],
    statusTag: [{ key: 1, value: 'success' }, { key: 2, value: 'danger' }],
    statusBtn: [{ key: '1', value: '' }, { key: '0', value: '' }]
  },
  exam: {
    examPaper: {
      paperTypeEnum: [{ key: 1, value: '' }, { key: 4, value: '' }, { key: 6, value: '' }]
    },
    question: {
      typeEnum: [{ key: 1, value: '' }, { key: 2, value: '' }, { key: 3, value: '' }, { key: 4, value: '' }, { key: 5, value: '' }],
      editUrlEnum: [{ key: 1, value: '/exam/question/edit/singleChoice', name: '' },
        { key: 2, value: '/exam/question/edit/multipleChoice', name: '' },
        { key: 3, value: '/exam/question/edit/trueFalse', name: '' },
        { key: 4, value: '/exam/question/edit/gapFilling', name: '' },
        { key: 5, value: '/exam/question/edit/shortAnswer', name: '' }]
    }
  }
}

// getters
const getters = {
  enumFormat: (state) => (arrary, key) => {
    return format(arrary, key)
  }
}

// actions
const actions = {}

// mutations
const mutations = {}

const format = function (array, key) {
  for (let item of array) {
    if (item.key === key) {
      return item.value
    }
  }
  return null
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



